import { NavsResponseInterface } from './../infrastructure/navs.interface';

export class NavsCheckUtil {
  static isScoreHighEnough(navsResult: NavsResponseInterface): boolean {
    return parseInt(navsResult.navsScore) > 75;
  }

  static fixedProductScoreCalculation(navsResult: NavsResponseInterface, score?: string): boolean {
    if (score) {
      return true;
    }
    if (parseInt(navsResult.navsScore) === 10) {
      return true;
    }
    return this.isScoreHighEnough(navsResult);
  }

  static standaloneMobileScoreCalculation(navsResult: NavsResponseInterface, prepaid: boolean): boolean {
    if (parseInt(navsResult.navsScore) < 10) {
      return false;
    }
    if (prepaid) {
      return parseInt(navsResult.navsScore) > 78;
    }
    return this.isScoreHighEnough(navsResult);
  }

  static getFormattedDOB(dob: Date | undefined): string {
    if (!dob) {
      return '';
    }
    const padWithZero = (num: number): string => (num < 10 ? `0${num}` : num.toString());
    const day = dob.getDate();
    const month = dob.getMonth() + 1;
    const year = dob.getFullYear();

    return `${padWithZero(day)}/${padWithZero(month)}/${year}`;
  }
}
