import { Injectable } from '@angular/core';
import { OcapiCallConfig, OcapiService } from '@telenet/ng-lib-ocapi';
import { Observable } from 'rxjs';
import { NavsResponseInterface, ResidentialNavsInterface } from './navs.interface';
import { map } from 'rxjs/operators';
import { NavsMapper } from './navs.mapper';

@Injectable({
  providedIn: 'root',
})
export class NavsService {
  url = '/public/api/customer-service/v1/customers/navs';

  constructor(private readonly ocapiService: OcapiService) {}

  getResidentialNavs(body: ResidentialNavsInterface): Observable<NavsResponseInterface> {
    return this.ocapiService
      .doPost(new OcapiCallConfig('', this.url, new NavsMapper(), body))
      .pipe(map((response) => response));
  }
}
